.container {
    -webkit-animation: fade-in 0.5s;
            animation: fade-in 0.5s;
    align-content: center;
    display: flex;
    flex-direction: column;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
    justify-content: space-around;
    margin: 5px;
    width: 100%;
    background-color: #1b1b1b;
    border: solid thin #000000;

}



.top {
    display: flex;
    flex-wrap: wrap-reverse;
    width: auto;
    flex-direction: inherit;
    position: -webkit-sticky;
    position: sticky;
    box-shadow: 1px 1px 1px black;
    top: 0;
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.84);

}

.left, .right {
    flex-wrap: wrap;
    text-align: left;

}

.left {
    flex-basis: 70%;
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.right {
    flex: 1 1;
    display: flex;
    flex-direction: column;

}

.right-sub {
    width: auto;
    margin: 4px 10px 4px 4px;
}

.middle {
    display: flex;
    flex-wrap: wrap;
    height: auto;
    justify-content: center
}

.prestige {
    margin: auto;
}

.logo {
    height: 100px;
}



@-webkit-keyframes fade-in {
    from {opacity: 0}
    to {opacity: 1}

}

@keyframes fade-in {
    from {opacity: 0}
    to {opacity: 1}

}

.card{
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
    margin: 10px;
    text-align: center;
    color: #a6a6a6;
    /*height: fit-content;*/
    background-color: #1f1f1f;
    width: -webkit-fill-available;
    max-width: 290px;

}


.card-sub {
    background-color: #2a2a2a;
    margin: 10px;
    text-wrap: normal;
}

.card-top {
    background-color: #2a2a2a;
}

.card-context {
    -webkit-animation: fade-in 2s linear;
            animation: fade-in 2s linear;
    -webkit-animation-duration: inherit;
            animation-duration: inherit;
    margin: 5px;
    color: white;
    font-size: 60px;
    height: auto;
    line-height: 111px;
    background-color: #1f1f1f;
}


.graph-mini {
    background-color: white;
    margin: 10px;
    color: white;
}


.App {
    color: orange;
    display: flex;
    flex-direction: row;
}

.player-list{
    display: inline;
    margin: 10px;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
}

.player-list:hover {
    -webkit-animation: colorChenger 2s linear infinite;
            animation: colorChenger 2s linear infinite;
}

@-webkit-keyframes fade-in {
    from {opacity: 0}
    to {
        opacity: 1}

}

@keyframes fade-in {
    from {opacity: 0}
    to {
        opacity: 1}

}
.graph {
    display: flex;
    flex-wrap: wrap;
    -webkit-animation: fade-in 2s ;
            animation: fade-in 2s ;
    justify-content: center;
}
.statistic-container{
    margin: 5px;
}


@media (max-width: 12450px) {
.App{
    color: orange;
    display: flex;
    flex-direction: row;
}
}

@media (max-width: 500px) {
    .App{
        color: orange;
        display: flex;
        flex-direction: column;
    }
}
